.team {
    max-width: 1132px;
    margin: 0 auto;
    width: 100%;
    padding: 0 10px;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        width: 500px;
        height: 500px;
        background: var(--color-1, #7E51FF);
        filter: blur(100px);
        top: 183px;
        left: -35%;
        transform: rotate(-60deg);
        z-index: 1;
    }

    .team__item--above {
        position: absolute;
        left: -260px;
        top: 35px;
        font-family: "proxima-bold";
        font-size: 80px;
        -webkit-text-stroke: 1px;
        -webkit-text-stroke-color: #fcffff;
        color: transparent;
        opacity: 0.2;
        white-space: nowrap;
        text-transform: uppercase;
    }

    .team__item--under {
        position: absolute;
        bottom: 15px;
        left: 51%;
        transform: translate(-50%, 74%);
        font-family: "proxima-bold";
        font-size: 80px;
        -webkit-text-stroke: 1px;
        -webkit-text-stroke-color: #fcffff;
        color: transparent;
        opacity: 0.2;
        white-space: nowrap;
        text-transform: uppercase;
    }

    .team__inner {
        display: flex;
        gap: 75px;

        .team__inner--info {
            margin-top: 105px;
            max-width: 540px;
            z-index: 2;

            .team__info--title {
                color: var(--text, #FCFFFF);
                font-family: "proxima-bold";
                font-size: 64px;
                font-style: normal;
                max-width: 496px;
                font-weight: 700;
                line-height: 65px;

            }

            .team__info--text {
                margin-top: 16px;
                color: var(--text, #FCFFFF);
                font-family: "proxima-bold";
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 28px;
                letter-spacing: -0.5px;
            }

            .team__info--btn {
                margin-top: 40px;
                display: flex;
                gap: 8px;
                width: 209px;
                padding: 18px 22px 19px 22px;
                justify-content: center;
                align-items: center;
                border-radius: 16px;
                background: var(--primary, linear-gradient(95deg, #68C9FF 9.27%, #D1B5FF 84.06%));
                color: var(--background, #1D1F21);
                font-family: "proxima-bold";
                font-size: 18px;
                font-style: normal;
                font-weight: 700;
                line-height: 26.5px;
            }
        }

        .team__inner--pic {
            margin-top: 100px;
            position: relative;
            max-width: 450px;
            width: 100%;
            height: 100%;

            .team__pic--img {
                width: 100%;
                object-fit: contain;
                position: relative;
                z-index: 2;
            }

            .promo__pic--star1 {
                position: absolute;
                left: -95px;
                bottom: 35px;
            }

            .promo__pic--star2 {
                position: absolute;
                right: -105px;
                top: 150px;
            }

            .promo__pic--circle {
                position: absolute;
                left: -150px;
                bottom: 35px;
            }

            &::before {
                content: '';
                width: 100%;
                height: 100%;
                transform: rotate(10deg);
                position: absolute;
                top: -5px;
                left: 60px;
                border-radius: 24px;
                background: url(../images/bgMain.png), lightgray 50% / cover no-repeat;
                z-index: 1;
            }
        }
    }
}

.benefits {
    max-width: 1132px;
    margin: 0 auto;
    margin-top: 105px;
    width: 100%;
    padding: 0 10px;
    position: relative;
    z-index: 10;


    .benefits__headline {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .benefits__headline--title {
            background: linear-gradient(130.10deg, rgb(104, 201, 255), rgb(209, 181, 255));
            -webkit-background-clip:
                text;
            -webkit-text-fill-color:
                transparent;
            background-clip:
                text;
            text-fill-color:
                transparent;
            font-family: "proxima-bold";
            font-size: 18px;
            font-weight: 700;
            line-height: 26.5px;
            letter-spacing: 1.2px;
        }

        .benefits__headline--subtitle {
            color: rgb(252, 255, 255);
            font-family: "proxima-bold";
            font-size: 36px;
            font-weight: 700;
            line-height: 49.5px;
            letter-spacing: 0%;
        }
    }

    .benefits__content {
        margin-top: 30px;
        width: 100%;
        display: flex;
        justify-content: space-between;

        .benefits__content--item {
            max-width: 255px;
            min-height: 255px;
            width: 100%;
            border-radius: 12px;
            background: rgb(33, 36, 38);
            padding: 18px;

            .content__item--svg {
                border-radius: 12px;
                background: linear-gradient(130.10deg, rgba(104, 201, 255, 0.1) 22.178%, rgba(209, 181, 255, 0.1) 92.41%);
                width: 48px;
                height: 48px;
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .contact__item--title {
                color: rgb(252, 255, 255);
                font-family: "proxima-bold";
                font-size: 20px;
                font-weight: 700;
                line-height: 30px;
                letter-spacing: 0%;
                padding-top: 10px;
            }

            .contact__item--desc {
                padding-top: 5px;
                max-width: 213px;
                width: 100%;
                color: rgb(252, 255, 255);
                font-family: "proxima-bold";
                font-size: 16px;
                font-weight: 400;
                line-height: 28px;
                letter-spacing: 0%;
            }
        }
    }
}

.section-5 {
    margin-top: 65px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 92px;
    position: relative;

    .contact_title {
        font-family: 'proxima-bold';
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.5px;
        letter-spacing: 1.8px;
        background: var(--primary, linear-gradient(95deg, #68C9FF 9.27%, #D1B5FF 84.06%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .contact_about {
        margin-top: 2px;
        color: var(--text, #FCFFFF);
        text-align: center;
        font-family: 'proxima-bold';
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 49.5px;
        max-width: 540px;
    }

    form {
        margin-top: 36px;
        display: flex;
        flex-direction: column;
        width: 410px;

        label {
            color: var(--secondary, #C8D2DC);
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;
        }

        .inputName {
            color: #FCFFFF;
            margin-top: 7px;
            width: 100%;
            padding: 21px 21px 22px 32px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;
            opacity: 0.6;
            border-radius: 9px;
            background: var(--frames, #212426);
            margin-bottom: 15px;
        }

        .inputMail {
            color: #FCFFFF;
            margin-top: 7px;
            width: 100%;
            padding: 21px 21px 22px 32px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;
            opacity: 0.6;
            border-radius: 9px;
            background: var(--frames, #212426);
            margin-bottom: 15px;
        }

        .inputText {
            color: #FCFFFF;
            margin-top: 7px;
            width: 100%;
            height: 182px;
            padding: 21px 21px 22px 32px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;
            opacity: 0.6;
            border-radius: 9px;
            background: var(--frames, #212426);
            margin-bottom: 15px;
            resize: none;
        }

        .submitBtn {
            color: var(--background, #1D1F21);
            font-family: 'proxima-bold';
            font-size: 18px;
            font-style: normal;
            font-weight: 700;
            line-height: 26.5px;
            padding: 18px 0px 19px 0px;
            border-radius: 16px;
            background: var(--primary, linear-gradient(95deg, #68C9FF 9.27%, #D1B5FF 84.06%));
        }

        .custom-checkbox {
            margin-top: 18px;
            display: flex;
            align-items: center;
            color: var(--secondary, #C8D2DC);
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;

            .checkmark {
                margin-left: 10px;
            }

            a {
                color: var(--color-3, #86A8FF);
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 21px;
                text-decoration-line: underline;
                margin-left: 5px;
                letter-spacing: 0.2px;
                margin-right: 4px;
            }

            .privasyCheckbox {
                -webkit-appearance: none;
                width: 20px;
                height: 20px;
                background: var(--background, #1D1F21);
                border-radius: 7px;
                border: 1px solid #525960;
                position: relative;

                &:checked {
                    &::before {
                        content: "\2713";
                        /* Unicode для галочки */
                        font-size: 16px;
                        /* Розмір галочки */
                        color: #68c9ff;
                        /* Колір галочки */
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -40%);
                    }
                }
            }
        }
    }

    .bg_img_con {
        position: absolute;
        width: 100px;
        height: 100px;

        .bg_img {
            position: absolute;
        }

        .dim21 {
            top: 203%;
            left: -795%;
        }


        .dim22 {
            top: 135%;
            left: -510%;
            filter: blur(6.5px)
        }

        .dim23 {
            top: 528%;
            left: -454%;
        }

        .cub24 {
            top: 41%;
            left: 420%;
            filter: blur(6.5px)
        }
    }
}

@media (max-width: 1100px) {
    .benefits .benefits__content {
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 11px;
    }
}

@media (max-width: 950px) {
    .team .team__inner {
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .team .team__inner .team__inner--info {
        align-self: flex-start;
    }

    .team .team__inner .team__inner--pic {
        margin-top: 0px;
    }
}

@media (max-width: 450px) {
    .benefits {
        margin-top: 65px;
        padding: 0 15px;
    }

    .benefits .benefits__headline {
        align-items: flex-start;

    }

    .benefits .benefits__headline .benefits__headline--title {
        font-size: 14px;
    }

    .benefits .benefits__headline .benefits__headline--subtitle {
        font-size: 26px;
        max-width: 225px;
        width: 100%;
        line-height: 34px;
    }

    .benefits .benefits__content {
        gap: 11px;
    }

    .benefits .benefits__content .benefits__content--item .content__item--svg {
        svg {
            width: 15px;
            height: 15px;
        }

        width: 36px;
        height: 36px;
    }

    .benefits .benefits__content .benefits__content--item {
        max-width: 167px;
        min-height: 187px;
        padding: 10px;
    }

    .benefits .benefits__content .benefits__content--item .contact__item--title {
        font-size: 14px;
        font-weight: 700;
        line-height: 20px;
        padding-top: 7px;
    }

    .benefits .benefits__content .benefits__content--item .contact__item--desc {
        font-size: 12px;
        font-weight: 400;
        line-height: 20px;
        padding-top: 0;
    }
}

@media (max-width: 376px) {
    .team {
        padding: 0 15px;
        margin-top: -10px;
    }

    .team .team__item--above {
        left: auto;
        right: -100px;
        top: 10px;
        font-size: 50px;
    }

    .team .team__inner {
        gap: 80px;
    }

    .team .team__inner .team__inner--info {
        margin-top: 70px;
    }

    .team::before {
        left: auto;
        right: -200px;
    }

    .team .team__inner .team__inner--info .team__info--title {
        font-size: 36px;
        max-width: 296px;
        width: 100%;
        line-height: 40px;
    }

    .team .team__inner .team__inner--info .team__info--text {
        font-size: 15px;
        font-weight: 400;
        line-height: 24px;
        margin-top: 10px;
        max-width: 345px;
        width: 100%;
    }

    .team .team__inner .team__inner--info .team__info--btn {
        margin-top: 30px;
        width: 193px;
        height: 52px;
        font-size: 15px;
        font-weight: 700;
        line-height: 26.5px;
    }

    .team .team__inner .team__inner--pic .promo__pic--star1 {
        top: -220px;
        left: -30px;
        width: 30px;
        height: 41px;
    }

    .team .team__inner .team__inner--pic .promo__pic--star2 {
        top: -220px;
        right: 35px;
        width: 30px;
        height: 41px;
        z-index: 10;
    }

    .team .team__inner .team__inner--pic .promo__pic--circle {
        left: 90px;
        z-index: 10;
        bottom: auto;
        top: -180px;
    }

    .team .team__inner .team__inner--pic {
        display: flex;

    }

    .team .team__inner .team__inner--pic .team__pic--img {
        width: 279px;
        height: 279px;
    }

    .team .team__inner .team__inner--pic::before {
        width: 285.57px;
        height: 286.21px;
        left: 40px;
    }

    .benefits .benefits__content {
        justify-content: flex-start;

    }

    .section-5 .bg_img_con {
        display: none;
    }

    .section-5 .contact_title {
        font-size: 14px;
    }

    .section-5 .contact_about {
        font-size: 26px;
        text-align: left;
        line-height: 34px;
        max-width: 306px;
    }

    .section-5 {
        max-width: 440px;
        width: 100%;
        padding: 0 15px;
        align-items: flex-start;
        margin-top: 65px;
        margin-bottom: 30px;
    }

    .section-5 form {
        width: 100%;
        margin-top: 15px;
    }
}